import React, { Component } from "react";
import { FiCheck } from "react-icons/fi";
import { HiOutlineShoppingBag } from "react-icons/hi";
import { MdFavorite } from "react-icons/md";
import { AiOutlineSearch } from "react-icons/ai";
import {BsListNested} from "react-icons/bs"
import Rightnavbar from "./Rightnavbar";
import ÇocukCategory from "./Çocuk";

import Erkek from "./Erkek";
import Kadın from "./Kadın";
import İndirimler from "./İndirimler";
import YeniÇıkanlar from "./Yeni Çıkanlar";
import Mainimage from "./main-image";

export default class Navbar extends Component {

 state={
   listShow:false,
   mouseOverListÇocuk:false,
   mouseOverListErkek:false,
   mouseOverListKadın:false,
   mouseOverListİndirimler:false,
   mouseOverListYeniÇıkanlar:false,
   showCarousel:false,
   showRight:false,
   inputVal:""
}

fixedChange=()=>{
  this.setState({showRight:!this.state.showRight});
 }  

showList=(e)=>{
  this.setState({listShow:!this.state.listShow})
  
} 
mouseOut=()=>{
  this.setState({showCarousel:false})
 }
 
mouseOverÇocuk=()=>{
  this.setState({mouseOverListÇocuk:!this.state.mouseOverListÇocuk});
  this.setState({mouseOverListErkek:false});
  this.setState({mouseOverListKadın:false});
  this.setState({mouseOverListİndirimler:false});
  this.setState({mouseOverListYeniÇıkanlar:false});
  this.setState({showCarousel:true});
  
} 

mouseOverErkek=()=>{
  this.setState({mouseOverListErkek:!this.state.mouseOverListErkek});
  this.setState({mouseOverListÇocuk:false});
  this.setState({mouseOverListKadın:false});
  this.setState({mouseOverListİndirimler:false});
  this.setState({mouseOverListYeniÇıkanlar:false});
  this.setState({showCarousel:!this.state.showCarousel})
  this.setState({showCarousel:!this.state.showCarousel});
  
}
mouseOverKadın=()=>{
  this.setState({mouseOverListKadın:!this.state.mouseOverListKadın});
  this.setState({mouseOverListÇocuk:false});
  this.setState({mouseOverListErkek:false});
  this.setState({mouseOverListİndirimler:false});
  this.setState({mouseOverListYeniÇıkanlar:false});
  this.setState({showCarousel:!this.state.showCarousel});
  
}
mouseOverİndirimler=()=>{
  this.setState({mouseOverListİndirimler:!this.state.mouseOverListİndirimler});
  this.setState({mouseOverListÇocuk:false});
  this.setState({mouseOverListKadın:false});
  this.setState({mouseOverListErkek:false});
  this.setState({mouseOverListYeniÇıkanlar:false});
  this.setState({showCarousel:!this.state.showCarousel});
  
} 
mouseOverYeniÇıkanlar=()=>{
  this.setState({mouseOverListYeniÇıkanlar:!this.state.mouseOverListYeniÇıkanlar});
  this.setState({mouseOverListÇocuk:false});
  this.setState({mouseOverListKadın:false});
  this.setState({mouseOverListErkek:false});
  this.setState({mouseOverListİndirimler:false});
  this.setState({showCarousel:!this.state.showCarousel});
}
handleSubmit=(e)=>{
  e.preventDefault();
  console.log(this.state.inputVal)
}

handleİnput=(e)=>{
this.setState({inputVal:e.target.value});
console.log(this.state.inputVal);
}

render() {
    return (
      <div>
        <div className="container-fluid navbar">
          <div className="float-left">
            <FiCheck size="45px" />
          </div>

          <div class="searchbar">
            <input
            onSubmit={this.handleSubmit}
            onChange={this.handleİnput}
            value={this.state.inputVal}
              class="search_input"
              type="text"
              name=""
              placeholder="Search..."
            />
            <a className="navbar-icons" href="/#" class="search_icon">
              <AiOutlineSearch color="black" size="30px" />
            </a>
          </div>

          <div className="float-right">
            <a className="navbar-icons" href="/">
              <HiOutlineShoppingBag  size="30px" />
            </a>
            <a className="navbar-icons" href="/">
              <MdFavorite  size="30px" />
            </a>
            
           <BsListNested onClick={()=>this.showList()} className="list" size="30px"/>
           
          </div>
        </div>
        <div className="nav-text" style={{ textAlign: "center" }}>
          <a onMouseOver={this.mouseOverÇocuk}className="text-a" href="/">
            Çocuk
           </a>
          <a onMouseOver={this.mouseOverErkek} className="text-a" href="/">
            Erkek
            
          </a>
          <a onMouseOver={this.mouseOverKadın} className="text-a" href="/">
            Kadın
          </a>
          <a onMouseOver={this.mouseOverİndirimler} className="text-a" href="/">
            İndirimler
          </a>
          <a onMouseOver={this.mouseOverYeniÇıkanlar} className="text-a" href="/">
            Yeni Çıkanlar
          </a>
        </div>
        <div>
            {
                this.state.listShow ? 
                 <Rightnavbar className="right-nav"showRight={this.state.showRight}/>
                :null
            }
            <div>
            {this.state.mouseOverListÇocuk ? <div><ÇocukCategory mouseOut={this.mouseOut} mouseOverÇocuk={this.mouseOverÇocuk}/></div>:null}
            {this.state.mouseOverListErkek ? <div><Erkek  mouseOut={this.mouseOut} mouseOverErkek={this.mouseOverErkek}/></div>:null}
              {this.state.mouseOverListKadın ? <div><Kadın  mouseOut={this.mouseOut} mouseOverKadın={this.mouseOverKadın}/></div>:null}
              {this.state.mouseOverListİndirimler ? <div><İndirimler  mouseOut={this.mouseOut} mouseOverİndirimler={this.mouseOverİndirimler}/></div>:null}
              {this.state.mouseOverListYeniÇıkanlar ? <div><YeniÇıkanlar  mouseOut={this.mouseOut} mouseOverYeniÇıkanlar={this.mouseOverYeniÇıkanlar}/></div>:null}
              </div>
           </div>
           <div className={this.state.showCarousel? "carousel-img2":"carousel-img1"}>
      <Mainimage/>
      </div>
     
      </div>
    );
  }
}
